.header-wrapper {
  height: 35px;
  width: 100vw;
  background-color: #007763;
  display: flex;
  padding-top: 5px;
  padding-bottom: 5px;
  div {
    cursor: pointer;
    display: flex;
    align-items: center;
    width: 35px;
    height: 100%;
    border-radius: 4px;
    margin-left: 4px;
    // margin-right: 2px;
    background-color: rgba(255, 255, 255, 0.315);
    transition: all 200ms ease-in-out;
    svg {
      width: 100%;
      margin: 0 auto;
      display: block;
      color: white;
      transition: all 200ms ease-in-out;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.5);
      svg{
          transform: scale(1.3);
      }
    }
  }
}
